import { ConfigProvider, Pagination, Table } from 'antd';
import React from 'react';
import styled from 'styled-components';

import EmptyTableIndicator from 'components/EmptyTableIndicator';
import I from 'components/I';
import colors from 'colors';

const SimpleTableContainer = styled.div`
  min-height: 540px;
  max-width: 1200px;
  width: 100%;
  background: #292b42 !important;
  display: table;
  margin: 0 auto;

  .ant-table-placeholder {
    border-style: none !important;
    user-select: none !important;
    background-color: #292b42 !important;

    .ant-empty-normal {
      margin: 64px 0px !important;
    }
  }

  .ant-empty-description {
    color: #696a9b !important;
  }
`;

const SimpleTable = styled(Table)`
  border-style: none !important;
  border-width: 0 !important;
  border-color: ${colors.foreground}important;
  min-height: 500px;

  .ant-table-thead > tr > th {
    background: linear-gradient(
      180deg,
      rgba(46, 47, 74, 1) 0%,
      rgba(37, 38, 59, 0.5) 100%
    ) !important;
    color: #696a9b;
    border-bottom-style: none !important;
    height: 35px !important;
    line-height: 22px !important;
    padding: 0px !important;
  }

  .ant-table-tbody > tr > td {
    border-bottom-style: none !important;
  }
`;

const Cell = styled.div`
  font-size: 0.9rem;
  margin-right: ${props => props.margin || 4}px;
  margin-left: ${props => props.margin || 4}px;
  white-space: nowrap;
`;

const Header = styled(Cell)`
  font-weight: 400;
  user-select: none;
  color: ${colors.textDim};
`;

const SimpleTableWidget = ({
  columnBuilders,
  rowData,
  emptyText,
  hasPagination,
  margin
}) => {
  const _rowData = rowData || [];
  const customizeRenderEmpty = () => <EmptyTableIndicator text={emptyText} />;

  const columns = columnBuilders.map((builder, j) => ({
    ...builder,
    title: (
      <Header margin={margin}>
        <I s={builder.label} />
      </Header>
    ),
    dataIndex: 'col_' + j
  }));

  const dataSource = _rowData.map((row, i) => {
    var rowValue = { key: i };

    columnBuilders.forEach((builder, j) => {
      rowValue['col_' + j] = (
        <Cell margin={margin}>{builder.getColValue(row)}</Cell>
      );
    });

    return rowValue;
  });

  return (
    <SimpleTableContainer>
      <ConfigProvider
        renderEmpty={dataSource.length === 0 && customizeRenderEmpty}
      >
        <SimpleTable
          tableLayout="auto"
          size="middle"
          dataSource={dataSource}
          columns={columns}
          pagination={false}
        />
        {dataSource.length > 10 ? (
          <Pagination
            style={{
              padding: '30px 0px',
              background: colors.background,
              textAlign: 'center'
            }}
            size=""
            total={dataSource.length}
            pageSize={10}
          />
        ) : (
          <div />
        )}
      </ConfigProvider>
    </SimpleTableContainer>
  );
};

export default SimpleTableWidget;
