import { fetchGasPrice } from 'redux/actions/GasPrice';
import { fetchMarketList } from 'redux/actions/market/MarketList';
import { fetchTokens } from 'redux/actions/Tokens';
import { updateColumns } from 'redux/actions/LayoutManager';
import { updatePrice } from 'redux/actions/TradePanel';

import { getEtherscanLink } from 'lightcone/api/localStorgeAPI';

import {
  LOGGED_IN,
  METAMASK_NOT_FOUND,
  NOT_REGISTERED,
  REGISTERED,
  REGISTERING,
  RESETTING_PASSWORD,
  UNDEFINED,
  WALLET_UNCONNECTED,
  getDataFromLocalStorage,
  updateAccount
} from 'redux/actions/DexAccount';

import { tracker } from 'components/DefaultTracker';

import './TopNavBar.less';
import {
  MenuFontAwesomeIcon,
  SideBarButton,
  SideBarDrawer,
  SideBarGroupLabel,
  SideBarGroupSeperator
} from '../SideBarDrawer';

const MyBlockies = styled(Blockies)`
  border-radius: 50%;
  width: 36px !important;
  height: 36px !important;
  border: 2px solid ${colors.textWhite};
`;

const AddressAvatarButton = ({ fullAddress, address }) => {
  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: AppLayout.topNavBarHeight
      }}
    >
      <MyBlockies seed={fullAddress} />

      <span
        style={{
          marginLeft: '12px',
          userSelect: 'none',
          color: colors.textWhite,
          fontSize: '0.9rem',
          fontWeight: '600'
        }}
      >
        {address}
      </span>
    </div>
  );
};

class EntranceButton extends React.Component {
  state = {
    buttonType: 'UNKNOWN'
  };

  defaultButton = (
    <BaseEntranceButton
      spin
      icon={faSpinner}
      title={
        <span style={{ color: colors.textDim }}>
          <I s="Loading..." />
        </span>
      }
      iconColor={colors.textDim}
      onClick={() => {}}
    />
  );
  pendingButtonJob = null;
  pendingButton = {
    type: this.state.buttonType,
    button: this.defaultButton
  };

  askMetaMaskConnect = () => {
    this.props.connectToMetaMask(true);
  };

  getWalletAddress = () => {
    let walletAddress = '';
    if (
      typeof this.props.dexAccount.account !== 'undefined' &&
      typeof this.props.dexAccount.account.address !== 'undefined'
    ) {
      walletAddress = this.props.dexAccount.account.address;
      walletAddress =
        walletAddress.substring(0, 7) + '...' + walletAddress.slice(-7);
    }
    return walletAddress;
  };

  pressedExportAccountButton = () => {
    this.props.showEnterPasswordModal(true);
    this.props.showSideBar(false);
  };

  pressedLogoutButton = () => {
    this.props.showLogoutModal(true);
    this.props.showSideBar(false);
  };

  pressedDepositButton = () => {
    this.props.showDepositModal(true);
    this.props.showSideBar(false);
  };

  pressedWithdrawButton = () => {
    this.props.showWithdrawModal(true);
    this.props.showSideBar(false);
  };
  pressedResetPasswordButton = () => {
    this.props.resetPasswordModal(true);
    this.props.showSideBar(false);
  };

  updateAccountStateInRedux(state) {
    const { dexAccount, updateAccount } = this.props;
    const { account } = dexAccount;
    updateAccount({
      ...account,
      state
    });
  }

  scheduleButtonUpdate(pendingButton) {
    if (this.pendingButton.type !== pendingButton.type) {
      this.pendingButton = pendingButton;
      clearTimeout(this.pendingButtonJob);

      this.pendingButtonJob = setTimeout(() => {
        this.setState({
          buttonType: pendingButton.type
        });

        tracker.trackEvent({
          type: 'USER_STATE',
          data: {
            state: pendingButton.type
          }
        });
      }, pendingButton.timeout || 1500);
    }
  }

  createButtonAndDrawer(pendingButton) {
    return (
      <div>
        {pendingButton.button()}
        {pendingButton.drawerContent ? (
          <SideBarDrawer
            header={pendingButton.drawerHeader()}
            body={pendingButton.drawerContent()}
            footer={
              pendingButton.drawerFooter ? (
                pendingButton.drawerFooter()
              ) : (
                <div />
              )
            }
            onClose={() => this.props.showSideBar(false)}
            visible={this.props.isSideBarVisible}
          />
        ) : (
          <span />
        )}
      </div>
    );
  }

  render() {
    const { account } = this.props.dexAccount;
    const { isDesiredNetwork } = this.props.metaMask;

    const myAddressLinks = (
      <div>
        <SideBarGroupSeperator />
        <SideBarGroupLabel>
          <I s="MenuMyEthAccount" />
        </SideBarGroupLabel>
        <SideBarButton
          key="myaddress"
          onClick={() => {
            window.open(
              `${getEtherscanLink(this.props.exchange.chainId)}/address/${
                this.props.dexAccount.account.address
              }`
            );
          }}
        >
          <MenuFontAwesomeIcon icon={faExternalLinkSquareAlt} />
          <I s="Show on Etherscan" />
        </SideBarButton>
      </div>
    );
    const commonLinks = (
      <div>
        <SideBarGroupSeperator />
        <SideBarGroupLabel>
          <I s="MenuSupportGroup" />
        </SideBarGroupLabel>

        <SideBarButton
          key="dexcongtract"
          onClick={() =>
            window.open(
              `${getEtherscanLink(this.props.exchange.chainId)}/address/${
                this.props.exchange.exchangeAddress
              }`,
              '_blank'
            )
          }
        >
          <MenuFontAwesomeIcon icon={faPencilRuler} />
          <I s="DEX Smart Contract (Beta1)" />
        </SideBarButton>

        <SideBarButton
          key="loopring"
          onClick={() => window.open('https://loopring.org', '_blank')}
        >
          <MenuFontAwesomeIcon icon={faRulerCombined} />
          <I s="Loopring Protocol" />
        </SideBarButton>

        <SideBarButton
          key="bug"
          onClick={() => {
            window.open(
              'https://github.com/Loopring/dex-ux/issues/new',
              '_blank'
            );
          }}
        >
          <MenuFontAwesomeIcon icon={faBug} />
          <I s="Report a Bug" />
        </SideBarButton>

        <SideBarButton
          key="legal"
          onClick={() => {
            window.open('/legal/terms', '_blank');
            this.props.showSideBar(false);
          }}
        >
          <MenuFontAwesomeIcon icon={faFileSignature} />
          <I s="Legal Documents" />
        </SideBarButton>

        <SideBarGroupSeperator />
        <SideBarGroupLabel>
          <I s="Documentation" />
        </SideBarGroupLabel>

        <SideBarButton
          hoverbg={colors.orange}
          colortxt={colors.orange}
          onClick={() => {
            window.open('/document/beta1', '_blank');
            this.props.showSideBar(false);
          }}
        >
          <MenuFontAwesomeIcon icon={faTrophy} />
          <I s="Beta1 Testing Rewards" />
        </SideBarButton>

        <SideBarButton
          key="fees"
          onClick={() => {
            window.open('/document/fees', '_blank');
            this.props.showSideBar(false);
          }}
        >
          <MenuFontAwesomeIcon icon={faFileAlt} />
          <I s="Fee Schedule" />
        </SideBarButton>

        <SideBarButton
          key="listing"
          onClick={() => {
            window.open('/document/listing', '_blank');
            this.props.showSideBar(false);
          }}
        >
          <MenuFontAwesomeIcon icon={faFileAlt} />
          <I s="Token Listing" />
        </SideBarButton>

        <SideBarButton
          key="api"
          disabled
          onClick={() => {
            window.open('/document/api', '_blank');
            this.props.showSideBar(false);
          }}
        >
          <MenuFontAwesomeIcon icon={faFileAlt} />
          <I s="Relayer API" />
        </SideBarButton>
      </div>
    );

    if (account.state === METAMASK_NOT_FOUND) {
      this.scheduleButtonUpdate({
        type: 'METAMASK_NOT_FOUND',
        button: () => (
          <div
            onMouseEnter={() => {
              this.props.showSideBar(true);
            }}
          >
            <BaseEntranceButton
              bgcolor={colors.red}
              icon={faUnlink}
              iconColor={colors.textBright}
              title={<I s="Web3 Unavailable" />}
            />
          </div>
        ),
        drawerHeader: () => {
          return (
            <BaseEntranceButton
              bgcolor={'transparent'}
              icon={faUnlink}
              title={<I s="Web3 Unavailable" />}
            />
          );
        },
        drawerContent: () => {
          return (
            <div>
              <div
                style={{
                  paddingTop: '32px',
                  width: '100%',
                  marginLeft: 'auto',
                  marginRight: 'auto',
                  textAlign: 'center'
                }}
              >
                <FontAwesomeIcon
                  style={{
                    color: colors.red
                  }}
                  size="3x"
                  icon={faUnlink}
                />
                <div
                  style={{
                    fontSize: '0.9rem',
                    margin: '32px 8px',
                    color: colors.textWhite
                  }}
                >
                  <I s="Web3UnavailableTip" />
                </div>
              </div>

              {commonLinks}
            </div>
          );
        }
      });
    } else if (
      account.state === WALLET_UNCONNECTED ||
      account.state === UNDEFINED
    ) {
      this.scheduleButtonUpdate({
        type: 'WALLET_UNCONNECTED',
        timeout: 2000,
        button: () => (
          <div
            onMouseEnter={() => {
              this.props.showSideBar(true);
            }}
          >
            <BaseEntranceButton
              icon={faPlug}
              bgcolor={colors.theme}
              title={<I s="Connect to Web3" />}
            />
          </div>
        ),
        drawerHeader: () => {
          return (
            <BaseEntranceButton
              bgcolor={'transparent'}
              icon={faPlug}
              title={<I s="Connect to Web3" />}
            />
          );
        },
        drawerContent: () => {
          return (
            <div>
              <div
                style={{
                  paddingTop: '32px',
                  width: '100%',
                  marginLeft: 'auto',
                  marginRight: 'auto',
                  textAlign: 'center'
                }}
              >
                <FontAwesomeIcon
                  style={{
                    color: colors.theme
                  }}
                  size="3x"
                  icon={faPlug}
                />
                <div
                  style={{
                    fontSize: '0.9rem',
                    margin: '32px',
                    color: colors.textWhite
                  }}
                >
                  <I s="ConnectToWeb3Tip" />
                </div>

                <div style={{ margin: '16px' }}>
                  <ActionButton onClick={() => this.askMetaMaskConnect()}>
                    <I s="Connect to Web3" />
                  </ActionButton>
                </div>
              </div>
              {myAddressLinks}

              {commonLinks}
            </div>
          );
        }
      });
    } else if (isDesiredNetwork) {
      switch (account.state) {
        case WALLET_UNCONNECTED:
          break;

        case RESETTING_PASSWORD:
          this.scheduleButtonUpdate({
            type: 'RESETTING_PASSWORD',
            button: () => (
              <div
                onMouseEnter={() => {
                  this.props.showSideBar(true);
                }}
              >
                <BaseEntranceButton
                  spin
                  icon={faCircleNotch}
                  iconColor={colors.theme}
                  title={<I s="Reseting Password..." />}
                />
              </div>
            ),
            drawerHeader: () => {
              return (
                <AddressAvatarButton
                  fullAddress={this.props.dexAccount.account.address}
                  address={this.getWalletAddress()}
                />
              );
            },
            drawerContent: () => {
              return (
                <div>
                  <div
                    style={{
                      paddingTop: '32px',
                      width: '100%',
                      marginLeft: 'auto',
                      marginRight: 'auto',
                      textAlign: 'center'
                    }}
                  >
                    <FontAwesomeIcon
                      style={{
                        color: colors.theme
                      }}
                      spin
                      size="3x"
                      icon={faCircleNotch}
                    />
                    <div
                      style={{
                        fontSize: '0.9rem',
                        margin: '32px',
                        color: colors.textWhite
                      }}
                    >
                      <I s="PasswordResetNotification" />
                    </div>

                    <div style={{ margin: '16px' }}>
                      <ActionButton
                        buttonbackground={colors.green}
                        onClick={this.pressedDepositButton}
                      >
                        <I s="Deposit" />
                      </ActionButton>
                    </div>
                  </div>
                  {myAddressLinks}

                  <SideBarGroupSeperator />
                  <SideBarGroupLabel>
                    <I s="MenuMyDexAccount" />
                  </SideBarGroupLabel>

                  <SideBarButton
                    key="deposit"
                    onClick={this.pressedDepositButton}
                  >
                    <MenuFontAwesomeIcon icon={faArrowAltCircleDown} />
                    <I s="Deposit" />
                  </SideBarButton>

                  {commonLinks}
                </div>
              );
            }
          });
          break;

        case NOT_REGISTERED:
          this.scheduleButtonUpdate({
            type: 'NOT_REGISTERED',
            button: () => (
              <div
                onMouseEnter={() => {
                  this.props.showSideBar(true);
                }}
              >
                <BaseEntranceButton
                  icon={faUserCircle}
                  bgcolor={colors.theme}
                  iconColor={colors.textBright}
                  title={this.getWalletAddress()}
                />
              </div>
            ),
            drawerHeader: () => {
              return (
                <AddressAvatarButton
                  fullAddress={this.props.dexAccount.account.address}
                  address={this.getWalletAddress()}
                />
              );
            },
            drawerContent: () => {
              return (
                <div>
                  <div
                    style={{
                      paddingTop: '32px',
                      width: '100%',
                      marginLeft: 'auto',
                      marginRight: 'auto',
                      textAlign: 'center'
                    }}
                  >
                    <FontAwesomeIcon
                      style={{
                        color: colors.theme
                      }}
                      size="3x"
                      icon={faUserCircle}
                    />
                    <div
                      style={{
                        fontSize: '0.9rem',
                        margin: '32px',
                        color: colors.textWhite
                      }}
                    >
                      <I s="NoAccountTooltip" />
                    </div>

                    <div style={{ margin: '16px' }}>
                      <ActionButton
                        onClick={() => this.props.registerAccountModal(true)}
                      >
                        <I s="Register Account" />
                      </ActionButton>
                    </div>
                  </div>
                  {myAddressLinks}

                  {commonLinks}
                </div>
              );
            }
          });
          break;

        case REGISTERING:
          // Lightcone API has the record for this address, however the browser doesn't have keys.
          this.scheduleButtonUpdate({
            type: 'REGISTERING',
            button: () => (
              <div
                onMouseEnter={() => {
                  this.props.showSideBar(true);
                }}
              >
                <BaseEntranceButton
                  spin
                  icon={faCircleNotch}
                  iconColor={colors.theme}
                  title={<I s="Registering Account..." />}
                />
              </div>
            ),
            drawerHeader: () => {
              return (
                <AddressAvatarButton
                  fullAddress={this.props.dexAccount.account.address}
                  address={this.getWalletAddress()}
                />
              );
            },
            drawerContent: () => {
              return (
                <div>
                  <div
                    style={{
                      paddingTop: '32px',
                      width: '100%',
                      marginLeft: 'auto',
                      marginRight: 'auto',
                      textAlign: 'center'
                    }}
                  >
                    <FontAwesomeIcon
                      style={{
                        color: colors.theme
                      }}
                      spin
                      size="3x"
                      icon={faCircleNotch}
                    />
                    <div
                      style={{
                        fontSize: '0.9rem',
                        margin: '32px',
                        color: colors.textWhite
                      }}
                    >
                      <I s="AccountBeingRegisteredNotification" />
                    </div>

                    <div style={{ margin: '16px' }}>
                      <ActionButton
                        buttonbackground={colors.green}
                        onClick={this.pressedDepositButton}
                      >
                        <I s="Deposit" />
                      </ActionButton>
                    </div>
                  </div>
                  {myAddressLinks}

                  <SideBarGroupSeperator />
                  <SideBarGroupLabel>
                    <I s="MenuMyDexAccount" />
                  </SideBarGroupLabel>

                  <SideBarButton
                    key="deposit"
                    onClick={this.pressedDepositButton}
                  >
                    <MenuFontAwesomeIcon icon={faArrowAltCircleDown} />
                    <I s="Deposit" />
                  </SideBarButton>

                  {commonLinks}
                </div>
              );
            }
          });
          break;
        case REGISTERED:
          // Lightcone API has the record for this address, however the browser doesn't have keys.
          this.scheduleButtonUpdate({
            type: 'REGISTERED',
            button: () => (
              <div
                onMouseEnter={() => {
                  this.props.showSideBar(true);
                }}
              >
                <BaseEntranceButton
                  icon={faLock}
                  iconColor={colors.red}
                  title={this.getWalletAddress()}
                />
              </div>
            ),
            drawerHeader: () => {
              return (
                <AddressAvatarButton
                  fullAddress={this.props.dexAccount.account.address}
                  address={this.getWalletAddress()}
                />
              );
            },
            drawerContent: () => {
              return (
                <div>
                  <div
                    style={{
                      paddingTop: '32px',
                      width: '100%',
                      marginLeft: 'auto',
                      marginRight: 'auto',
                      textAlign: 'center'
                    }}
                  >
                    <FontAwesomeIcon
                      style={{
                        color: colors.red
                      }}
                      size="3x"
                      icon={faLock}
                    />
                    <div
                      style={{
                        fontSize: '0.9rem',
                        margin: '32px',
                        color: colors.textWhite
                      }}
                    >
                      <I s="NeedLoginTooltip" />
                    </div>

                    <div style={{ margin: '16px' }}>
                      <ActionButton
                        onClick={() => {
                          this.props.showLoginModal(true);
                          this.props.showSideBar(false);
                        }}
                      >
                        <I s="Login" />
                      </ActionButton>
                    </div>
                  </div>

                  {myAddressLinks}

                  <SideBarGroupSeperator />
                  <SideBarGroupLabel>
                    <I s="MenuMyDexAccount" />
                  </SideBarGroupLabel>

                  <SideBarButton
                    key="reset"
                    onClick={() => this.pressedResetPasswordButton()}
                  >
                    <MenuFontAwesomeIcon icon={faKey} />
                    <I s="Reset Password" />
                  </SideBarButton>

                  {commonLinks}
                </div>
              );
            }
          });
          break;
        case LOGGED_IN:
          const lastVIPAccountId = 1100;
          const iconColor =
            this.props.dexAccount.account.accountId <= lastVIPAccountId
              ? colors.orange
              : colors.green;
          const icon =
            this.props.dexAccount.account.accountId <= lastVIPAccountId
              ? faCrown
              : faLockOpen;

          const vipBadge =
            this.props.dexAccount.account.accountId <= lastVIPAccountId ? (
              <div
                style={{
                  color: colors.orange,
                  fontSize: '0.75rem',
                  paddingTop: '4px',
                  textTransform: 'uppercase',
                  fontWeight: '600'
                }}
              >
                <I s="VIP4" />

                <div
                  style={{
                    fontSize: '0.75rem',
                    paddingTop: '4px',
                    padding: '20px',
                    opacity: '0.8',
                    textTransform: 'none'
                  }}
                >
                  <I s="VIP4Tip" />
                </div>
              </div>
            ) : (
              <span />
            );

          this.scheduleButtonUpdate({
            type: 'LOGGED_IN',
            button: () => (
              <div
                onMouseEnter={() => {
                  this.props.showSideBar(true);
                }}
              >
                <BaseEntranceButton
                  icon={icon}
                  iconColor={iconColor}
                  title={this.getWalletAddress()}
                />
              </div>
            ),

            drawerHeader: () => {
              return (
                <AddressAvatarButton
                  fullAddress={this.props.dexAccount.account.address}
                  address={this.getWalletAddress()}
                />
              );
            },

            drawerContent: () => {
              return (
                <div>
                  <div
                    style={{
                      paddingTop: '32px',
                      width: '100%',
                      marginLeft: 'auto',
                      marginRight: 'auto',
                      textAlign: 'center'
                    }}
                  >
                    <FontAwesomeIcon
                      style={{
                        color: iconColor
                      }}
                      size="3x"
                      icon={icon}
                    />
                    {vipBadge}
                    <div
                      style={{
                        fontSize: '0.9rem',
                        margin: '32px',
                        color: colors.textWhite
                      }}
                    >
                      <I s="YouHaveLoggedInTooltip" />
                    </div>
                    <div style={{ margin: '16px' }}>
                      <ActionButton
                        buttonbackground={colors.red}
                        onClick={() => this.props.showLogoutModal(true)}
                      >
                        <I s="Logout" />
                      </ActionButton>
                    </div>
                  </div>
                  {myAddressLinks}

                  <SideBarGroupSeperator />
                  <SideBarGroupLabel>
                    <I s="MenuMyDexAccount" />
                  </SideBarGroupLabel>

                  <SideBarButton
                    key="deposit"
                    onClick={this.pressedDepositButton}
                  >
                    <MenuFontAwesomeIcon icon={faArrowAltCircleDown} />
                    <I s="Deposit" />
                  </SideBarButton>

                  <SideBarButton
                    key="withdraw"
                    onClick={this.pressedWithdrawButton}
                  >
                    <MenuFontAwesomeIcon icon={faArrowAltCircleUp} />
                    <I s="Withdraw" />
                  </SideBarButton>

                  <SideBarButton
                    key="reset"
                    onClick={() => this.pressedResetPasswordButton()}
                  >
                    <MenuFontAwesomeIcon icon={faKey} />
                    <I s="Reset Password" />
                  </SideBarButton>

                  <SideBarButton
                    key="export"
                    onClick={() => this.pressedExportAccountButton()}
                  >
                    <MenuFontAwesomeIcon icon={faCode} />
                    <I s="Export Account" />
                  </SideBarButton>

                  {commonLinks}
                </div>
              );
            }
          });

          break;

        default:
          console.error('unhandled account state ', account.state);
      }
    } else if (isDesiredNetwork === false) {
      const desiredNetwork = this.props.exchange.chainId;
      let netWorkName = 'EthereumMainNet';
      if (desiredNetwork === 4) netWorkName = 'EthereumRinkebyTestnet';
      else if (desiredNetwork === 5) netWorkName = 'EthereumGrliTestNet';

      this.scheduleButtonUpdate({
        type: 'WRONG_NETWORK',
        button: () => (
          <div
            onMouseEnter={() => {
              this.props.showSideBar(true);
            }}
          >
            <BaseEntranceButton
              bgcolor={colors.red}
              icon={faWaveSquare}
              title={<I s="Wrong Network" />}
            />
          </div>
        ),
        drawerHeader: () => {
          return (
            <BaseEntranceButton
              bgcolor={'transparent'}
              icon={faWaveSquare}
              title={<I s="Wrong Network" />}
            />
          );
        },
        drawerContent: () => {
          return (
            <div>
              <div
                style={{
                  paddingTop: '32px',
                  width: '100%',
                  marginLeft: 'auto',
                  marginRight: 'auto',
                  textAlign: 'center'
                }}
              >
                <FontAwesomeIcon
                  style={{
                    color: colors.red
                  }}
                  size="3x"
                  icon={faWaveSquare}
                />
                <div
                  style={{
                    fontSize: '0.9rem',
                    margin: '32px',
                    color: colors.textWhite
                  }}
                >
                  <span>
                    <I s="UnsupportedNetworkWarning" />
                    <I s={netWorkName} />
                  </span>
                </div>
              </div>

              {commonLinks}
            </div>
          );
        }
      });
    }

    return (
      <div>
        {this.state.buttonType === 'UNKNOWN'
          ? this.defaultButton
          : this.createButtonAndDrawer(this.pendingButton)}
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { pathname } = state.router.location;
  const { dexAccount, metaMask, exchange, modalManager } = state;
  const isSideBarVisible = modalManager.isSideBarVisible;

  return {
    pathname,
    dexAccount,
    metaMask,
    exchange,
    isSideBarVisible
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchAccounts: () => dispatch(fetchAccounts()),
    connectToMetaMask: startConnecting =>
      dispatch(connectToMetaMask(startConnecting)),
    showExportAccountModal: show => dispatch(showExportAccountModal(show)),
    showEnterPasswordModal: show => dispatch(showEnterPasswordModal(show)),
    showLogoutModal: show => dispatch(showLogoutModal(show)),
    resetPasswordModal: show => dispatch(resetPasswordModal(show)),
    showDepositModal: show => dispatch(showDepositModal(show)),
    showWithdrawModal: show => dispatch(showWithdrawModal(show)),
    registerAccountModal: show => dispatch(registerAccountModal(show)),
    showLoginModal: show => dispatch(loginModal(show)),
    getDataFromLocalStorage: address =>
      dispatch(getDataFromLocalStorage(address)),
    fetchTokens: () => dispatch(fetchTokens()),
    fetchMarketList: () => dispatch(fetchMarketList()),
    updatePrice: price => dispatch(updatePrice(price)),
    updateColumns: () => dispatch(updateColumns()),
    fetchGasPrice: () => dispatch(fetchGasPrice()),
    fetchCmcPrice: legal => dispatch(fetchCmcPrice(legal)),
    updateAccount: account => dispatch(updateAccount(account)),
    fetchAllExchangeInfo: () => dispatch(fetchAllExchangeInfo()),
    showSideBar: show => dispatch(showSideBar(show))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(EntranceButton);
