import { connect } from 'react-redux';
import React from 'react';
import styled from 'styled-components';

import { Button, ConfigProvider, Menu } from 'antd';
import AppLayout from 'AppLayout';
import I from 'components/I';
import colors from 'colors';

import { history } from 'redux/configureStore';
import SiteLogo from 'components/top-nav-bar/SiteLogo';

import { fetchAllExchangeInfo } from 'redux/actions/ExchangeInfo';
import ConfirmLogoutModal from 'modals/LogoutModal';
import DepositModal from 'modals/DepositModal';
import DexAccountBalanceService from 'components/services/DexAccountBalanceService';
import DexAccountOrderService from 'components/services/DexAccountOrderService';
import DexAccountService from 'components/services/DexAccountService';
import EnterPasswordModal from 'modals/EnterPasswordModal';
import ExportAccountModal from 'modals/ExportAccountModal';
import HealthCheckService from 'components/services/HealthCheckService';
import LoginModal from 'modals/LoginModal';
import MetaMaskService from 'components/services/MetaMaskService';
import RegisterAccountModal from 'modals/RegisterAccountModal';
import ResetPasswordModal from 'modals/ResetPasswordModal';
import WebSocketService from 'components/services/WebSocketService';
import WithdrawModal from 'modals/WithdrawModal';

import { fetchCmcPrice } from 'redux/actions/CmcPrice';
import { fetchGasPrice } from 'redux/actions/GasPrice';
import { fetchMarketList } from 'redux/actions/market/MarketList';
import { fetchNonce } from 'redux/actions/Nonce';
import { fetchTokens } from 'redux/actions/Tokens';

import { LOGGED_IN } from 'redux/actions/DexAccount';

import { getRefreshDurationInMillionSeconds } from 'lightcone/api/LightconeAPI';

import './TopNavBar.less';
import { NavButtonWrapper } from 'styles/Styles';
import EntranceButton from 'components/top-nav-bar/EntranceButton';

const NavButton = styled(Button)`
  padding-left: 8px !important;
  padding-right: 8px !important;
  font-size: 1.2rem !important;
  font-weight: 600 !important;
  background-color: transparent !important;
  height: ${AppLayout.topNavBarHeight}!important;
  border: none !important;
  border-radius: 0 !important;
  border-bottom-style: solid !important;
  border-bottom-width: 3px !important;
  border-bottom-color: ${props =>
    props.active === 1 ? colors.theme + '!important' : 'transparent!important'};

  &:hover {
    color: ${colors.textBright}!important;
  }

  &[disabled] {
    color: ${colors.textDim}!important;
  }
`;

class TopNavBar extends React.Component {
  componentDidMount() {
    this.mounted = true;
    this.props.fetchAllExchangeInfo();
    this.mainFunctionToCallAPI();
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (
      this.props.dexAccount.account.state !==
        nextProps.dexAccount.account.state ||
      this.props.pathname !== nextProps.pathname
    ) {
      return true;
    } else {
      return false;
    }
  }

  // MetaMask related code will be moved to MetaMaskService
  mainFunctionToCallAPI = () => {
    (async () => {
      try {
        await this.props.fetchTokens();
        await this.props.fetchMarketList();
        this.props.fetchCmcPrice(this.props.cmcPrice.legal);
      } catch (error) {
      } finally {
        this.tick();
      }
    })();
  };

  tick = () => {
    try {
      if (
        typeof window.wallet !== 'undefined' &&
        typeof window.wallet.address !== 'undefined'
      ) {
        (async () => {
          this.props.fetchNonce(window.wallet.address);
          this.props.fetchGasPrice();
        })();
      }

      this.props.fetchCmcPrice(this.props.cmcPrice.legal);
    } catch (error) {
    } finally {
      if (this.mounted) {
        setTimeout(
          () => this.tick.bind(this)(),
          getRefreshDurationInMillionSeconds()
        );
      }
    }
  };

  render() {
    const { account } = this.props.dexAccount;

    const onTradePage = this.props.pathname.includes('/trade/');
    const disableTopTabs =
      this.props.pathname.includes('/document/') ||
      this.props.pathname.includes('/legal/');

    return (
      <div>
        <ConfigProvider>
          <div
            className="desktop-layout"
            style={{
              borderTop: `0px none ${colors.border}`,
              borderBottomStyle: 'none'
            }}
            ref={menuButton => (this._settingsButtonElement = menuButton)}
          >
            <RegisterAccountModal />
            <LoginModal />
            <DepositModal />
            <ResetPasswordModal />
            <WithdrawModal />
            <EnterPasswordModal />
            <MetaMaskService />
            <DexAccountBalanceService />
            <DexAccountOrderService />
            <DexAccountService />
            <HealthCheckService />
            <WebSocketService {...this.props} />

            <ConfirmLogoutModal />
            <ExportAccountModal />
            <Menu
              mode="horizontal"
              style={{
                background: colors.background
              }}
            >
              <NavButtonWrapper
                key="logo"
                style={{
                  width: `calc(${AppLayout.tradePanelWidth})`,
                  height: AppLayout.topNavBarHeight,
                  background:
                    onTradePage || disableTopTabs
                      ? colors.sidePanelBackground
                      : colors.background,
                  borderRightStyle: 'solid',
                  borderRightWidth: '1px',
                  borderRightColor:
                    onTradePage || disableTopTabs
                      ? colors.background
                      : colors.foreground
                }}
              >
                <SiteLogo showSettingIcon={onTradePage || disableTopTabs} />
              </NavButtonWrapper>

              <NavButtonWrapper key="trade" disabled={disableTopTabs}>
                <NavButton
                  active={onTradePage ? 1 : 0}
                  onClick={() => {
                    history.push('/trade/' + this.props.currentMarket);
                  }}
                >
                  <I s="Trade" />
                </NavButton>
              </NavButtonWrapper>
              <NavButtonWrapper key="orders" disabled={disableTopTabs}>
                <NavButton
                  active={this.props.pathname.includes('orders') ? 1 : 0}
                  onClick={() => {
                    history.push('/orders/open-orders');
                  }}
                  disabled={account.state !== LOGGED_IN}
                >
                  <I s="Orders" />
                </NavButton>
              </NavButtonWrapper>
              <NavButtonWrapper key="account" disabled={disableTopTabs}>
                <NavButton
                  active={this.props.pathname.includes('account') ? 1 : 0}
                  onClick={() => {
                    history.push('/account/balances');
                  }}
                  disabled={account.state !== LOGGED_IN}
                >
                  <I s="Account" />
                </NavButton>
              </NavButtonWrapper>

              {/*The next component should be in a seperate file*/}
              <NavButtonWrapper
                key="alipay"
                style={{
                  float: 'right',
                  paddingLeft: '0px',
                  paddingRight: '0px',
                  height: '50px'
                }}
              >
                <EntranceButton />
              </NavButtonWrapper>
            </Menu>
          </div>
          <div className="mobile-layout">
            <Menu
              mode="horizontal"
              style={{
                background: colors.background
              }}
            >
              <NavButtonWrapper
                key="logo"
                style={{
                  width: `calc(${AppLayout.tradePanelWidth})`,
                  height: AppLayout.topNavBarHeight,
                  background: colors.background,
                  borderRightStyle: 'solid',
                  borderRightWidth: '1px',
                  borderRightColor: colors.foreground
                }}
              >
                <SiteLogo />
              </NavButtonWrapper>
            </Menu>
          </div>
        </ConfigProvider>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { pathname } = state.router.location;
  const { dexAccount, cmcPrice, market } = state;
  return {
    pathname,
    dexAccount,
    cmcPrice,
    currentMarket: market.currentMarket.current
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchTokens: () => dispatch(fetchTokens()),
    fetchMarketList: () => dispatch(fetchMarketList()),
    fetchNonce: address => dispatch(fetchNonce(address)),
    fetchGasPrice: () => dispatch(fetchGasPrice()),
    fetchCmcPrice: legal => dispatch(fetchCmcPrice(legal)),
    fetchAllExchangeInfo: () => dispatch(fetchAllExchangeInfo())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TopNavBar);
