import { connect } from 'react-redux';
import { history } from 'redux/configureStore';
import React from 'react';

import { getRefreshDurationInMillionSeconds } from 'lightcone/api/LightconeAPI';
import I from 'components/I';

import { emptyTradePanel } from 'redux/actions/TradePanel';
import { fetchCandles } from 'redux/actions/market/Candle';
import { fetchMarketList } from 'redux/actions/market/MarketList';
import { fetchTokens } from 'redux/actions/Tokens';
import { setMarket } from 'redux/actions/market/CurrentMarket';
import { updateOrderBooksLevel } from 'redux/actions/market/OrderBook';

import { notifyError } from 'redux/actions/Notification';

class MarketService extends React.Component {
  componentDidMount() {
    this.mounted = true;
    this.mainFunctionToCallApi();
    this.checkMarketValid();
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.market.marketList.marketNames.length > 0) {
      const newMarket = this.props.match.params.pair;
      if (
        this.props.market.marketList.marketNames.includes(newMarket) === false
      ) {
        notifyError(
          <span>
            {newMarket} <I s="IsNotAValidMarket" />
          </span>
        );
        history.push('/404');
        return;
      }
    }

    if (
      this.props.match.params.pair.toUpperCase() !==
        this.props.market.currentMarket.current ||
      prevProps.match.params.pair !== this.props.match.params.pair
    ) {
      const market = this.props.match.params.pair;
      this.props.setMarket(market);
      this.props.fetchCandles(market, this.props.tokens);
      this.props.updateOrderBooksLevel(market, 0);
      this.props.emptyTradePanel();
    }
  }

  checkMarketValid = () => {
    (async () => {
      try {
        await this.props.fetchTokens();
        await this.props.fetchMarketList();
      } catch (err) {}
    })();
  };

  // Move this api calls to each components
  mainFunctionToCallApi = () => {
    if (this.mounted) {
      const { market } = this.props;
      this.props.fetchCandles(market.currentMarket.current, this.props.tokens);
      setTimeout(
        () => this.mainFunctionToCallApi(),
        getRefreshDurationInMillionSeconds()
      );
    }
  };

  render() {
    return <div />;
  }
}

const mapStateToProps = state => {
  const { market, exchange } = state;
  return { market, tokens: exchange.tokens };
};

const mapDispatchToProps = dispatch => {
  return {
    setMarket: marketName => dispatch(setMarket(marketName)),
    fetchTokens: () => dispatch(fetchTokens()),
    fetchMarketList: () => dispatch(fetchMarketList()),
    fetchCandles: (market, tokens) => dispatch(fetchCandles(market, tokens)),
    updateOrderBooksLevel: (market, level) =>
      dispatch(updateOrderBooksLevel(market, level)),
    emptyTradePanel: () => dispatch(emptyTradePanel())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MarketService);
