import { connect } from 'react-redux';
import { history } from 'redux/configureStore';
import I from 'components/I';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

import config from 'lightcone/config';
import i18n from '../../i18n';

import { Button, Col, Popover, Row, Tooltip } from 'antd';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCog } from '@fortawesome/free-solid-svg-icons/faCog';
import { faGlobeAsia } from '@fortawesome/free-solid-svg-icons/faGlobeAsia';
import { getLanguage, saveLanguage } from 'lightcone/api/localStorgeAPI';
import { updateColor } from 'redux/actions/ColorManager';
import AppLayout from 'AppLayout';

import { tracker } from 'components/DefaultTracker';

const LanguageButton = styled(Button)`
  font-size: 0.8rem!important;
  font-weight: 600!important;
  background-color: #3E3F61!important;
  margin: 16px 8px;
  color: ${colors.textWhite}
  border: none!important;
  &[disabled], &:hover {
    border: none;
    background-color: ${colors.theme}!important;
  }

  &[disabled] {
    color: ${colors.textWhite}!important;
  }
`;

const MyFontAwesomeIcon = styled(FontAwesomeIcon)`
  margin-left: 32px;
  margin-bottom: -6px;
`;

class SiteLogo extends React.Component {
  state = {
    isSettingsVisible: false
  };

  changeLanguage = value => {
    if (value === 'en' || value === 'zh') {
      i18n.changeLanguage(value);
      saveLanguage(value);
      this.props.updateColor(value);
    }

    this.setState({
      isSettingsVisible: false
    });
  };

  handleSettingsVisibleChange = isSettingsVisible => {
    this.setState({ isSettingsVisible });

    tracker.trackEvent({
      type: 'LOCATION_CHANGE',
      data: {
        location: 'settings-popover',
        visible: isSettingsVisible
      }
    });
  };

  render() {
    return (
      <div
        style={{
          paddingLeft: '4px',
          fontWeight: 700,
          fontSize: '0.9rem',
          color: colors.textWhite,
          height: AppLayout.topNavBarHeight,
          lineHeight: AppLayout.topNavBarHeight,
          userSelect: 'none'
        }}
      >
        <div>
          <img
            src="/assets/images/logo.svg"
            alt="Loopring"
            draggable="false"
            style={{
              width: '100px',
              height: 'auto',
              marginLeft: '-8px',
              paddingRight: '8px',
              paddingBottom: '4px',
              userSelect: 'none'
            }}
            onClick={() => {
              history.push('/trade/LRC-ETH');
            }}
          />
          <Tooltip
            style={{
              width: '400px'
            }}
            overlayStyle={{
              color: colors.textWhite,
              fontSize: '0.85rem'
            }}
            mouseEnterDelay={2}
            title={
              <div>
                <div>LAST_COMMIT={process.env.COMMITHASH}</div>
                <div>DEPLOYED={process.env.TIME}</div>
                <div>RELAYER={config.getRelayerHost()}</div>
              </div>
            }
          >
            <span
              style={{
                fontSize: '0.8rem',
                fontWeight: 400,
                marginRight: '32px',
                minWidth: '50px',
                display: 'inline-block',
                color: colors.theme
              }}
            >
              <I s="Beta1" />
            </span>
          </Tooltip>

          <span
            style={{
              display: this.props.showSettingIcon ? 'inline-block' : 'none'
            }}
          >
            {/*<MyFontAwesomeIcon icon={faBell} />*/}

            <Popover
              className="desktop-layout"
              placement={'bottomLeft'}
              overlayClassName="settings-popover"
              content={
                <div style={{ minWidth: '360px' }}>
                  <Row type="flex" justify="space-between">
                    <Col span={3}>
                      <FontAwesomeIcon
                        icon={faGlobeAsia}
                        style={{
                          width: '24px',
                          height: '24px',
                          color: colors.textWhite
                        }}
                      />
                    </Col>
                    <Col span={14}>
                      <div
                        style={{
                          fontSize: '1rem',
                          color: colors.textWhite
                        }}
                      >
                        <I s="Language" />
                      </div>
                      <div
                        style={{
                          fontSize: '0.85rem',
                          color: colors.textDim,
                          margin: '8px 0px'
                        }}
                      >
                        <I s="Change the default interface language" />
                      </div>
                    </Col>
                    <Col span={2}>
                      <LanguageButton
                        disabled={getLanguage() === 'en'}
                        size="small"
                        onClick={() => {
                          this.changeLanguage('en');

                          tracker.trackEvent({
                            type: 'CHANGE_LANGUAGE',
                            data: {
                              lang: 'en'
                            }
                          });
                        }}
                      >
                        En
                      </LanguageButton>
                    </Col>
                    <Col span={3}>
                      <LanguageButton
                        disabled={getLanguage() === 'zh'}
                        size="small"
                        onClick={() => {
                          this.changeLanguage('zh');

                          tracker.trackEvent({
                            type: 'CHANGE_LANGUAGE',
                            data: {
                              lang: 'zh'
                            }
                          });
                        }}
                      >
                        
                      </LanguageButton>
                    </Col>
                  </Row>
                </div>
              }
              title={<I s="Settings" />}
              trigger="hover"
              visible={this.state.isSettingsVisible}
              onVisibleChange={this.handleSettingsVisibleChange}
            >
              <MyFontAwesomeIcon
                icon={faCog}
                style={{ width: '14px', height: '14px' }}
              />
            </Popover>
          </span>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { pathname } = state.router.location;
  return {
    pathname
  };
};

const mapDispatchToProps = dispatch => {
  return {
    updateColor: language => dispatch(updateColor(language))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SiteLogo);
