import { getTimestamp } from 'lightcone/api/LightconeAPI';
import MaintenanceModal from 'modals/MaintenanceModal';
import React from 'react';

export default class HealthCheckService extends React.Component {
  state = {
    isVisible: false
  };

  componentDidMount() {
    this.mounted = true;
    this.mainFunctionToCallApi();
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  mainFunctionToCallApi = () => {
    (async () => {
      try {
        await getTimestamp();
        if (this.mounted) {
          this.setState({
            isVisible: false
          });
        }
      } catch (err) {
        if (this.mounted) {
          this.setState({
            isVisible: false
          });
        }
      } finally {
        if (this.mounted) {
          setTimeout(() => this.mainFunctionToCallApi(), 60 * 1000);
        }
      }
    })();
  };

  render() {
    return (
      <div>
        <MaintenanceModal isVisible={this.state.isVisible} />
      </div>
    );
  }
}
