import { history } from 'redux/configureStore';
import I from 'components/I';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

import { tracker } from 'components/DefaultTracker';

import { Button } from 'antd';

const NavButton = ({ selected, id, label, href }) => {
  const TheButton = styled(Button)`
    background-color: transparent !important;
    height: 46px !important;
    margin-right: 10px !important;
    margin-left: 10px !important;
    line-height: 46px !important;
    color: ${colors.textDim}!important;
    border-width: 0 !important;
    border-style: none !important;
    font-weight: 600 !important;
    font-size: 0.9rem !important;

    &:hover {
      color: ${colors.theme}!important;
    }
    &[disabled] {
      color: ${colors.theme}!important;
    }
  `;

  return (
    <TheButton
      disabled={selected === id}
      onClick={() => {
        history.push(href);
        tracker.trackEvent({
          type: 'LOCATION_CHANGE',
          data: {
            location: href
          }
        });
      }}
    >
      {label}
    </TheButton>
  );
};

const SecondaryNavBar = ({ selected, subPages }) => {
  return (
    <div
      style={{
        paddingLeft: '260px',
        paddingRight: '60px',
        paddingBottom: '0px',
        borderWidth: '0px',
        backgroundColor: colors.foreground
      }}
    >
      <Button.Group
        style={{
          borderRadius: '0px',
          borderWidth: '0px',
          paddingBottom: '0px'
        }}
      >
        {subPages.map((config, i) => (
          <NavButton
            key={i}
            selected={selected}
            id={config.id}
            href={config.url}
            label={<I s={config.label} />}
          />
        ))}
      </Button.Group>
    </div>
  );
};

export default SecondaryNavBar;
