import { Button, Tooltip } from 'antd';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import PropTypes from 'prop-types';
import React from 'react';
import colors from 'colors';
import styled from 'styled-components';

import AppLayout from 'AppLayout';
const MyButton = styled(Button)`
  font-size: 0.9rem !important;
  font-weight: 600 !important;
  border: none !important;
  height: calc(${AppLayout.topNavBarHeight} - 24px);
  margin-top: 12px;
  margin-bottom: auto;
  margin-right: 16px;
  min-width: 164px;
  padding: auto;
  cursor: default !important;
  color: ${props =>
    props.bgcolor ? colors.textBright : colors.textWhite}!important;
  background-color: ${props => props.bgcolor || colors.foreground}!important;
  &:hover {
    background-color: ${props =>
      props.bgcolor || colors.buttonBackground}!important;
    color: ${colors.textBright}!important;
  }

  &[disabled] {
    cursor: default !important;
    background-color: ${props => props.bgcolor || colors.foreground}!important;
    color: ${colors.textBright}!important;
  }
`;

export default class BaseEntranceButton extends React.Component {
  render() {
    return (
      <Tooltip
        overlayStyle={{
          color: colors.textWhite,
          fontSize: '0.85rem'
        }}
        placement="leftBottom"
        title={this.props.tooltip}
      >
        <MyButton
          bgcolor={this.props.bgcolor}
          disabled={this.props.disabled || false}
          onClick={() => (this.props.onClick ? this.props.onClick() : {})}
        >
          <div
            style={{
              lineHeight: '32px',
              display: 'block',
              width: '100%',
              verticalAlign: 'middle'
            }}
          >
            <FontAwesomeIcon
              style={{
                width: '14px',
                height: '14px',
                marginRight: '12px'
              }}
              color={this.props.iconColor}
              icon={this.props.icon}
              spin={this.props.spin || false}
            />
            <div
              style={{
                display: 'inline-block',
                textAlign: 'center'
              }}
            >
              {this.props.title}
            </div>
          </div>
        </MyButton>
      </Tooltip>
    );
  }
}

BaseEntranceButton.defaultProps = {
  onClick: () => {}
};

BaseEntranceButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.object]).isRequired
};
